import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import './FooterButtons.css';

const FooterButtons = () => {
  const location = useLocation();
  const navigate = useNavigate();

  const isActive = (path) => location.pathname === path;

  return (
    <div className="footer-buttons">
      <button 
        className={`menu-button ${isActive('/otes') ? 'active' : ''}`} 
        onClick={() => navigate('/otes')}
      >
        <i className="menu-icon ri-numbers-fill"></i>
        <span></span>
      </button>

      <button 
        className={`menu-button center ${isActive('/') ? 'active' : ''}`} 
        onClick={() => navigate('/')}
      >
        <div className="glow-wrapper">
          <div className="glow-circle"></div>
          <i className="menu-icon ri-briefcase-4-fill"></i>
        </div>
        <span></span>
      </button>

      <button 
        className={`menu-button ${isActive('/set') ? 'active' : ''}`} 
        onClick={() => navigate('/set')}
      >
        <i className="menu-icon ri-grid-fill"></i>
        <span></span>
      </button>
    </div>
  );
};

export default FooterButtons;
