import React, { useState, useEffect, useCallback, useRef } from 'react';
import { useSwipeable } from 'react-swipeable';
import { AnimatePresence, motion } from 'framer-motion';
import './Main.css';

const weekdays = ['', '', '', '', '', '', ''];
const iconOptions = ['', '', '', '', ''];

//    
const shiftTypes = [
  { value: 'regular', label: '' },
  { value: 'dayoff', label: '' },
  { value: 'sick', label: '' },
  { value: 'vacation', label: '' },
];

//     
const shiftTemplates = [
  { label: ' ', start: '09:00', end: '17:00' },
  { label: ' ', start: '22:00', end: '06:00' },
];

//   
const ShiftFormModal = ({
  date,
  onClose,
  onSave,
  initialData,
  editingShiftIndex,
  onDelete,
}) => {
  const [title, setTitle] = useState(initialData?.title || '');
  const [icon, setIcon] = useState(initialData?.icon || iconOptions[0]);
  const [startTime, setStartTime] = useState(initialData?.startTime || '');
  const [endTime, setEndTime] = useState(initialData?.endTime || '');
  const [color, setColor] = useState(initialData?.color || '#00b894');
  const [type, setType] = useState(initialData?.type || 'regular');
  const [tips, setTips] = useState(initialData?.tips || '');
  const [earned, setEarned] = useState(initialData?.earned || '');

  //    ( )
  const getDuration = () => {
    if (!startTime || !endTime) return '';
    let [startH, startM] = startTime.split(':').map(Number);
    let [endH, endM] = endTime.split(':').map(Number);

    let startTotal = startH * 60 + startM;
    let endTotal = endH * 60 + endM;

    //   (, 22:00 - 06:00)
    if (endTotal <= startTotal) {
      endTotal += 24 * 60;
    }

    let diff = endTotal - startTotal;
    const h = Math.floor(diff / 60);
    const m = diff % 60;
    return `${h} ${m}`;
  };

  const submit = () => {
    if (!title.trim() || !startTime || !endTime) {
      alert('  , !');
      return;
    }

    //  
    const [startH, startM] = startTime.split(':').map(Number);
    const [endH, endM] = endTime.split(':').map(Number);
    if (
      startH > endH ||
      (startH === endH && startM >= endM && !(startH === 22 && endH === 6))
    ) {
      if (!(startH === 22 && endH === 6)) {
        alert('     !');
        return;
      }
    }

    onSave(
      { title: title.trim(), icon, startTime, endTime, color, type, tips, earned },
      editingShiftIndex
    );
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <motion.div
        className="shift-form-modal"
        onClick={(e) => e.stopPropagation()}
        initial={{ scale: 0.8, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        exit={{ scale: 0.8, opacity: 0 }}
        transition={{ duration: 0.25 }}
      >
        <h3>
          {editingShiftIndex !== null ? ' ' : ' '} {' '}
          {date.toLocaleDateString('ru-RU')}
        </h3>

        <label>
           
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder=":  "
            autoFocus
          />
        </label>

        <label>
           
          <select value={icon} onChange={(e) => setIcon(e.target.value)}>
            {iconOptions.map((ic) => (
              <option key={ic} value={ic}>
                {ic}
              </option>
            ))}
          </select>
        </label>

        <label>
           
          <select value={type} onChange={(e) => setType(e.target.value)}>
            {shiftTypes.map((st) => (
              <option key={st.value} value={st.value}>
                {st.label}
              </option>
            ))}
          </select>
        </label>

        <label>
           
          <input
            type="color"
            value={color}
            onChange={(e) => setColor(e.target.value)}
          />
        </label>

        <div className="shift-templates" style={{ marginBottom: 12 }}>
          {shiftTemplates.map((tpl) => (
            <button
              key={tpl.label}
              type="button"
              className="btn-template"
              onClick={() => {
                setStartTime(tpl.start);
                setEndTime(tpl.end);
              }}
              style={{
                marginRight: 8,
                padding: '6px 10px',
                cursor: 'pointer',
                borderRadius: 5,
                border: '1px solid #ccc',
                background: '#f0f0f0',
              }}
            >
              {tpl.label}: {tpl.start}{tpl.end}
            </button>
          ))}
        </div>

        <label>
          
          <input
            type="time"
            value={startTime}
            onChange={(e) => setStartTime(e.target.value)}
          />
        </label>

        <label>
          
          <input
            type="time"
            value={endTime}
            onChange={(e) => setEndTime(e.target.value)}
          />
        </label>

        <label>
           ()
          <input
            type="number"
            value={tips}
            onChange={(e) => setTips(e.target.value)}
            placeholder=": 200"
          />
        </label>

        <label>
           ()
          <input
            type="number"
            value={earned}
            onChange={(e) => setEarned(e.target.value)}
            placeholder=": 3000"
          />
        </label>



        <div style={{ marginTop: 10 }}>
          : <b>{getDuration() || ''}</b>
        </div>

        <div
          className="modal-buttons"
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            marginTop: 20,
            alignItems: 'center',
          }}
        >
          <div>
            <button className="btn-save" onClick={submit}>
              
            </button>
            <button className="btn-cancel" onClick={onClose} style={{ marginLeft: 8 }}>
              
            </button>
          </div>
          {editingShiftIndex !== null && (
            <button
              className="btn-delete"
              onClick={() => {
                if (window.confirm(' ?')) {
                  onDelete(editingShiftIndex);
                }
              }}
              style={{
                backgroundColor: '#d63031',
                color: 'white',
                border: 'none',
                padding: '6px 10px',
                borderRadius: '5px',
                cursor: 'pointer',
                fontWeight: '700',
              }}
            >
              
            </button>
          )}
        </div>
      </motion.div>
    </div>
  );
};

const Main = () => {
  const [weeks, setWeeks] = useState([]);
  const [currentWeekIndex, setCurrentWeekIndex] = useState(0);
  const [direction, setDirection] = useState(0);
  const [today] = useState(new Date());
  const [events, setEvents] = useState({});
  const [shifts, setShifts] = useState({}); // { '2025-07-30': [{title, icon, startTime, endTime, color, type}, ...], ... }
  const [modalOpen, setModalOpen] = useState(false);
  const [selectedDate, setSelectedDate] = useState(null);
  const [modalPos, setModalPos] = useState({ top: 0, left: 0 });

  const [shiftFormOpen, setShiftFormOpen] = useState(false);
  const [shiftDate, setShiftDate] = useState(null);
  const [editingShiftIndex, setEditingShiftIndex] = useState(null);

  const weekContainerRef = useRef(null);
  const selectedDayRef = useRef(null);

  const getWeeks = (num) => {
    const today = new Date();
    const startOffset = (today.getDay() + 6) % 7;
    const start = new Date(today);
    start.setDate(start.getDate() - startOffset);
    start.setHours(0, 0, 0, 0);

    return Array.from({ length: num }, (_, i) => {
      const weekStart = new Date(start);
      weekStart.setDate(weekStart.getDate() + i * 7);
      return { start: weekStart };
    });
  };

  const getCurrentWeekIndex = useCallback(
    (weeksArr) => {
      return weeksArr.findIndex((week) => {
        const start = new Date(week.start);
        const end = new Date(start);
        end.setDate(end.getDate() + 6);
        return today >= start && today <= end;
      });
    },
    [today]
  );

  useEffect(() => {
    const w = getWeeks(20);
    setWeeks(w);
    const index = getCurrentWeekIndex(w);
    setCurrentWeekIndex(index !== -1 ? index : 0);
  }, [getCurrentWeekIndex]);

  const handleNextWeek = useCallback(() => {
    setDirection(1);
    setCurrentWeekIndex((prev) => Math.min(prev + 1, weeks.length - 1));
  }, [weeks]);

  const handlePrevWeek = useCallback(() => {
    setDirection(-1);
    setCurrentWeekIndex((prev) => Math.max(prev - 1, 0));
  }, []);

  const handleWheel = (e) => {
    if (e.deltaY > 0) handleNextWeek();
    else handlePrevWeek();
  };

  const handlers = useSwipeable({
    onSwipedUp: handleNextWeek,
    onSwipedDown: handlePrevWeek,
    trackTouch: true,
    trackMouse: false,
  });

  const getWeekNumber = (date) => {
    const d = new Date(date);
    d.setHours(0, 0, 0);
    d.setDate(d.getDate() + 4 - (d.getDay() || 7));
    const yearStart = new Date(d.getFullYear(), 0, 1);
    return Math.ceil(((d - yearStart) / 86400000 + 1) / 7);
  };

  const isCurrentWeek = () => {
    const index = getCurrentWeekIndex(weeks);
    return index === currentWeekIndex;
  };

  const formatDateKey = (date) => date.toISOString().split('T')[0];

  const handleDayClick = (date, event) => {
    setSelectedDate(date);
    setModalOpen(true);
    selectedDayRef.current = event.currentTarget;

    if (weekContainerRef.current && event.currentTarget) {
      const containerRect = weekContainerRef.current.getBoundingClientRect();
      const targetRect = event.currentTarget.getBoundingClientRect();
      const top = targetRect.top - containerRect.top + targetRect.height + 8;
      const left = targetRect.left - containerRect.left + targetRect.width / 2;
      setModalPos({ top, left });
    }
  };

  const handleAdd = (type) => {
    if (!selectedDate) return;
    if (type === 'shift') {
      setModalOpen(false);
      setShiftDate(selectedDate);
      setEditingShiftIndex(null);
      setShiftFormOpen(true);
    } else if (type === 'event') {
      const key = formatDateKey(selectedDate);
      const prevEvent = events[key] || '';
      const newEvent = prompt(`   ${key}`, prevEvent);
      if (newEvent !== null) {
        setEvents((prev) => ({
          ...prev,
          [key]: newEvent.trim(),
        }));
      }
      setModalOpen(false);
      setSelectedDate(null);
      selectedDayRef.current = null;
    }
  };

  const handleCloseModal = () => {
    setModalOpen(false);
    setSelectedDate(null);
    selectedDayRef.current = null;
  };

  //        
  const handleShiftClick = (dateKey, index) => {
    setShiftDate(new Date(dateKey));
    setEditingShiftIndex(index);
    setShiftFormOpen(true);
  };

  //  (  ) 
  const handleSaveShift = (shiftData, editIndex) => {
    const key = formatDateKey(shiftDate);
    setShifts((prev) => {
      const prevShifts = prev[key] ? [...prev[key]] : [];

      if (editIndex !== null && editIndex !== undefined) {
        prevShifts[editIndex] = shiftData;
      } else {
        prevShifts.push(shiftData);
      }

      return {
        ...prev,
        [key]: prevShifts,
      };
    });
    setShiftFormOpen(false);
    setShiftDate(null);
    setEditingShiftIndex(null);
  };

  //  
  const handleDeleteShift = (deleteIndex) => {
    const key = formatDateKey(shiftDate);
    setShifts((prev) => {
      const prevShifts = prev[key] ? [...prev[key]] : [];
      prevShifts.splice(deleteIndex, 1);
      return {
        ...prev,
        [key]: prevShifts,
      };
    });
    setShiftFormOpen(false);
    setShiftDate(null);
    setEditingShiftIndex(null);
  };

  const currentWeek = weeks[currentWeekIndex];
  if (!currentWeek) return null;

  return (
    <div className="main-container" {...handlers} onWheel={handleWheel}>
      <div className="calendar-header">
        <h2>{isCurrentWeek() ? ' ' : ''}</h2>
        <p>
          {currentWeek.start.toLocaleDateString('ru-RU', {
            day: 'numeric',
            month: 'long',
          })}
          ,  {getWeekNumber(currentWeek.start)}
        </p>
      </div>

      <div className="week-container" ref={weekContainerRef}>
        <AnimatePresence mode="wait" initial={false}>
          <motion.div
            key={currentWeekIndex}
            className="week-block"
            initial={{ opacity: 0, y: direction > 0 ? 50 : -50 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: direction > 0 ? -50 : 50 }}
            transition={{ duration: 0.35, ease: 'easeInOut' }}
          >
            {weekdays.map((dayName, index) => {
              const day = new Date(currentWeek.start);
              day.setDate(day.getDate() + index);
              const isToday = day.toDateString() === today.toDateString();
              const isSelected =
                selectedDate && day.toDateString() === selectedDate.toDateString();
              const dayKey = formatDateKey(day);
              const eventText = events[dayKey] || '';
              const dayShifts = shifts[dayKey] || [];

              return (
                <div
                  key={index}
                  className={`day-row ${isToday ? 'current-day' : ''} ${
                    isSelected ? 'selected-day' : ''
                  }`}
                  onClick={(e) => handleDayClick(day, e)}
                >
                  <div className="day-left">
                    <div className="day-name">{dayName}</div>
                    <div className="day-date">{day.getDate()}</div>
                  </div>
                  <div className="day-events">
                    {dayShifts.map((shift, i) => (
                      <div
                        key={i}
                        className="shift-item"
                        style={{
                          borderLeft: `5px solid ${shift.color || '#00b894'}`,
                          paddingLeft: 8,
                          cursor: 'pointer',
                          marginBottom: 4,
                          userSelect: 'none',
                        }}
                        title={`: ${
                          shiftTypes.find((st) => st.value === shift.type)?.label || ''
                        }`}
                        onClick={(e) => {
                          e.stopPropagation();
                          handleShiftClick(dayKey, i);
                        }}
                      >
                        <span className="shift-icon">{shift.icon}</span>{' '}
                        <span className="shift-title">{shift.title}</span>{' '}
                        <span className="shift-time">
                          ({shift.startTime} - {shift.endTime})
                        </span>{' '}
                        <span className="shift-type" style={{ fontStyle: 'italic', fontSize: 12 }}>
                          [{shiftTypes.find((st) => st.value === shift.type)?.label || ''}]
                        </span>
                      </div>
                    ))}
                    <div>{eventText}</div>
                  </div>
                  <div className="day-divider" />
                </div>
              );
            })}
          </motion.div>
        </AnimatePresence>
      </div>

      {/*    */}
      {modalOpen && selectedDate && (
        <div className="modal-overlay" onClick={handleCloseModal}>
          <div
            className="choice-modal"
            onClick={(e) => e.stopPropagation()}
            style={{ top: modalPos.top, left: modalPos.left, transform: 'translateX(-50%)' }}
          >
            <i className="ri-home-fill choice-icon" />
            <button className="choice-btn" onClick={() => handleAdd('shift')}>
              <span className="plus">+</span> 
            </button>
            <div className="choice-divider" />
            <button className="choice-btn" onClick={() => handleAdd('event')}>
              <span className="plus">+</span> 
            </button>
          </div>
        </div>
      )}

      {/*   */}
      <AnimatePresence>
        {shiftFormOpen && shiftDate && (
          <ShiftFormModal
            key="shiftForm"
            date={shiftDate}
            initialData={
              editingShiftIndex !== null
                ? shifts[formatDateKey(shiftDate)][editingShiftIndex]
                : null
            }
            editingShiftIndex={editingShiftIndex}
            onClose={() => {
              setShiftFormOpen(false);
              setEditingShiftIndex(null);
              setShiftDate(null);
            }}
            onSave={handleSaveShift}
            onDelete={handleDeleteShift}
          />
        )}
      </AnimatePresence>
    </div>
  );
};

export default Main;
