import React, { useState, useEffect, useRef } from "react";
import "./Otes.css";
import {
  CheckCircle,
  AlertCircle,
  Flame,
  ChevronDown,
} from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";

const Otes = () => {
  const today = new Date();
  const [month, setMonth] = useState(today.getMonth());
  const [year, setYear] = useState(today.getFullYear());
  const [key, setKey] = useState(`${today.getFullYear()}-${today.getMonth()}`);
  const [goals, setGoals] = useState({ hours: 0, pay: 0, tips: 0, percent: 0, bonus: 0 });
  const [inputs, setInputs] = useState({ hours: "0", pay: "0", tips: "0", percent: "0", bonus: "0" });
  const [saving, setSaving] = useState({ hours: false, pay: false, tips: false, percent: false, bonus: false });
  const [focus, setFocus] = useState(null);
  const [visible, setVisible] = useState({ tips: false, percent: false, bonus: false });
  const [showAddOptions, setShowAddOptions] = useState(false);
  const [goalPanelOpen, setGoalPanelOpen] = useState(false);
  const [direction, setDirection] = useState(0);
  const timeouts = useRef({});

  const workedHours = 120, hourlyRate = 20, tips = 150, salesPercent = 0.05, totalSales = 5000, bonus = 300;
  const basePay = workedHours * hourlyRate;
  const percentBonus = totalSales * salesPercent;
  const totalPay = basePay + tips + percentBonus + bonus;

  const formatCurrency = (val) =>
    new Intl.NumberFormat("ru-RU", {
      minimumFractionDigits: val % 1 !== 0 ? 2 : 0,
      maximumFractionDigits: val % 1 !== 0 ? 2 : 0,
    }).format(val);

  const getStatus = (val, goal) => {
    if (!goal) return null;
    if (val < goal) return { text: " ", color: "otes-yellow", icon: <AlertCircle size={14} /> };
    if (val === goal) return { text: "", color: "otes-green", icon: <CheckCircle size={14} /> };
    return { text: "", color: "otes-red", icon: <Flame size={14} /> };
  };

  useEffect(() => {
    for (const key in goals) {
      if (focus !== key) {
        setInputs((prev) => ({ ...prev, [key]: goals[key] ? String(goals[key]) : "" }));
      }
    }
  }, [goals, focus]);

  const changeMonth = (dir) => {
    let m = month + dir, y = year;
    if (m > 11) { m = 0; y++; }
    if (m < 0) { m = 11; y--; }
    setMonth(m);
    setYear(y);
    setKey(`${y}-${m}`);
    setDirection(dir);
  };

  const handleInput = (key) => (e) => {
    const val = e.target.value;
    setInputs((prev) => ({ ...prev, [key]: val }));
    setSaving((prev) => ({ ...prev, [key]: true }));
    clearTimeout(timeouts.current[key]);
    timeouts.current[key] = setTimeout(() => {
      setGoals((prev) => ({ ...prev, [key]: parseFloat(val) || 0 }));
      setSaving((prev) => ({ ...prev, [key]: false }));
    }, 1000);
  };

  const renderInput = (label, key) => (
    <div className="otes-input-group">
      <label>
        {label} {saving[key] && <span className="otes-saving-dot"> </span>}
      </label>
      <input
        type="number"
        min="0"
        value={inputs[key]}
        onFocus={() => setFocus(key)}
        onBlur={() => setFocus(null)}
        onChange={handleInput(key)}
      />
    </div>
  );

  const start = new Date(year, month, 1);
  const end = new Date(year, month + 1, 0);
  const range = `${start.toLocaleDateString("ru-RU", { day: "2-digit", month: "long" })}  ${end.toLocaleDateString("ru-RU", { day: "2-digit", month: "long", year: "numeric" })}`;

  const cardList = [
    { key: "hours", title: "", value: workedHours, show: true, color: "", raw: workedHours },
    { key: "pay", title: "", value: totalPay, show: true, color: "otes-green", raw: totalPay },
    { key: "tips", title: "", value: tips, show: visible.tips, color: "otes-white", raw: tips },
    { key: "percent", title: "  ", value: percentBonus, show: visible.percent, color: "otes-white", raw: percentBonus },
    { key: "bonus", title: "", value: bonus, show: visible.bonus, color: "otes-white", raw: bonus },
  ];

  return (
    <div className="otes-wrapper" onContextMenu={(e) => e.preventDefault()}>
      <h1 className="otes-title"> </h1>

      <div className="otes-date-range fade-in otes-date-left">
        <motion.button whileTap={{ scale: 0.9 }} onClick={() => changeMonth(-1)} className="otes-arrow-button">
          <ChevronDown style={{ transform: "rotate(90deg)" }} size={14} />
        </motion.button>

        

        <span>{range}</span>
        <motion.button whileTap={{ scale: 0.9 }} onClick={() => changeMonth(1)} className="otes-arrow-button">
          <ChevronDown style={{ transform: "rotate(-90deg)" }} size={14} />
        </motion.button>
      </div>

      <motion.div
        className="otes-slider-wrapper"
        layout
        initial={{ opacity: 0, x: direction > 0 ? 300 : -300 }}
        animate={{ opacity: 1, x: 0 }}
        exit={{ opacity: 0, x: direction > 0 ? -300 : 300 }}
        transition={{ duration: 0.4, ease: "easeInOut" }}
        key={key}
      >
        <AnimatePresence initial={false} custom={direction} mode="wait">
          <motion.div
            layout
            key={key}
            className="otes-grid"
            drag="x"
            dragConstraints={{ left: 0, right: 0 }}
            dragElastic={0.3}
            onDragEnd={(e, info) => {
              if (info.offset.x < -100) changeMonth(1);
              if (info.offset.x > 100) changeMonth(-1);
            }}
          >
            <div className="otes-card fade-in">
              <div
                className="otes-card-title"
                onClick={() => setGoalPanelOpen((v) => !v)}
                style={{
                  cursor: "pointer",
                  justifyContent: "center",
                  borderBottom: goalPanelOpen ? "1px solid #ffffff22" : "none",
                  paddingBottom: "4px",
                }}
              >
                <div style={{ display: "flex", alignItems: "center" }}>
                  <i className="ri-add-fill" style={{ fontSize: "20px" }}></i>
                  <span style={{ marginLeft: "8px" }}> </span>
                </div>
              </div>
              <div className={`otes-slide-panel ${goalPanelOpen ? "open" : ""}`}>
                {renderInput(" :", "hours")}
                {renderInput("  ():", "pay")}
                {visible.tips && renderInput("  ():", "tips")}
                {visible.percent && renderInput(" %   ():", "percent")}
                {visible.bonus && renderInput("  ():", "bonus")}
              </div>
            </div>

            {cardList.filter((c) => c.show).map(({ key, title, value, color, raw }) => {
              const progress = goals[key] ? (raw / goals[key]) * 100 : 0;
              const status = getStatus(raw, goals[key]);
              const barColor = !goals[key] ? "otes-grey" : raw >= goals[key] ? "otes-green" : "otes-yellow";
              return (
                <div key={key} className="otes-card fade-in">
                  {["tips", "percent", "bonus"].includes(key) && (
                    <button
                      className="otes-close-card"
                      onClick={() => {
                        setVisible((v) => ({ ...v, [key]: false }));
                        setGoals((g) => ({ ...g, [key]: 0 }));
                        setInputs((i) => ({ ...i, [key]: "0" }));
                      }}
                    >
                      <i className="ri-close-line"></i>
                    </button>
                  )}
                  <div className="otes-card-title" style={{ justifyContent: "center" }}>{title}</div>
                  <div className={`otes-value ${color}`}>
                    {formatCurrency(value)} <span className="unit">{key === "hours" ? "" : ""}</span>
                  </div>
                  <div className="otes-progress-info">
                    <div className="otes-progress">
                      <div className={`otes-progress-bar ${barColor}`} style={{ width: `${Math.min(progress, 100).toFixed(1)}%` }} />
                    </div>
                    <div className="otes-goal-status-row" style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginTop: "8px", gap: "10px" }}>
                      {status ? (
                        <div className={`otes-status-badge ${status.color}`}>{status.icon}<span>{status.text}</span></div>
                      ) : (
                        <div className="otes-status-badge otes-grey"><i className="ri-close-circle-line" style={{ marginRight: "5px", fontSize: "14px" }}></i><span>  </span></div>
                      )}
                      <div className="otes-goal-inline" style={{ textAlign: "right" }}>
                        <span className="otes-goal-label">:</span>{" "}
                        <span className="otes-goal-value">{formatCurrency(goals[key])} {key === "hours" ? "" : ""}</span>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </motion.div>
        </AnimatePresence>
      </motion.div>

      <div style={{ display: "flex", justifyContent: "center", marginTop: "20px" }}>
        {showAddOptions && (
          <div className="otes-overlay">
            <motion.div className="otes-modal" initial={{ scale: 0.8, opacity: 0 }} animate={{ scale: 1, opacity: 1 }} exit={{ scale: 0.8, opacity: 0 }} transition={{ duration: 0.3 }}>
              <button className="otes-modal-close" onClick={() => setShowAddOptions(false)}><i className="ri-close-line"></i></button>
              <h3> ?</h3>
              <div className="otes-modal-buttons">
                {Object.entries(visible).map(([key, val]) =>
                  !val && (
                    <button key={key} onClick={() => { setVisible((v) => ({ ...v, [key]: true })); setShowAddOptions(false); }}>
                      {key === "tips" ? "" : key === "percent" ? "  " : ""}
                    </button>
                  )
                )}
              </div>
            </motion.div>
          </div>
        )}
        {!showAddOptions && (
          <button onClick={() => setShowAddOptions(true)} className="otes-add-tip-btn">
            <i className="ri-add-circle-fill otes-plus-icon"></i>
          </button>
        )}
      </div>

      <div className="otes-quote">" . . ."</div>
    </div>
  );
};

export default Otes;
